export default {
	data() {
		return {
			imgUrls: ["/static/images/banner.jpg", "/static/images/banner.jpg", "/static/images/banner.jpg"],
			// 控制指示点
			indicatorDots: true,
			// 是否自动轮播
			autoplay: true,
			// 自动切换的时间
			interval: 3000,
			// 是否衔接滑动
			circular: true,
			// 滑动动画时长
			duration: 2000,
			// 指示点的颜色
			indicatorColor: '#ffffff',
			bannerData: null,
			// index: 0,
			// 需要渲染的省市区
			// array: ['合肥市'],
			// 控制默认渲染的城市的选择与隐藏
			isFlag: true,
			// 用户中心纬度
			latitude: null,
			// 用户中心经度,
			longitude: null,
			list: [{
					id: 1,
					url: '../../static/images/green-orderlistA.png',
					weight: '红水蜜桃3个...',
					num: '3个装...',
					new: '¥29.9',
					once: '¥39.9'
				},
				{
					id: 2,
					url: '../../static/images/green-orderlistB.png',
					weight: '红水蜜桃3个...',
					num: '3个装...',
					new: '¥29.9',
					once: '¥39.9'
				},
				{
					id: 3,
					url: '../../static/images/1.png',
					weight: '红水蜜桃3个...',
					num: '3个装...',
					new: '¥29.9',
					once: '¥39.9'
				},
				{
					id: 4,
					url: '../../static/images/green-orderlistB.png',
					weight: '红水蜜桃3个...',
					num: '3个装...',
					new: '¥29.9',
					once: '¥39.9'
				}
			],
			// 首页会员套餐列表数据
			goodsList: [],
			// 首页轮播图数据列表
			indexSwiperList: [],
			// 首页其他 banner 的id
			banner_id: '',
			bannerImg: ''
		};
	},
	onLoad() {
		// 获取所有会员套餐信息接口
		this.getFindList()
		// 获取首页轮播图数据接口
		this.getHeadBanner(),
		// 获取首页其他banner数据接口
		this.getCenterBanner(),
		// 获取用户位置
		this.isGetLocation()
	},
	methods: {
		// 去首页banner的详情
		goIndexBanner() {
			uni.navigateTo({
				url: '/pages/indexBanner/indexBanner?banner_id=' + this.banner_id
			})
		},
		// 去首页轮播图的详情
		goindexSwiperBanner(id) {
			console.log(id)
			uni.navigateTo({
				url: '/pages/indexSwiperBanner/indexSwiperBanner?id=' + id
			})
		},
		// 获取所有会员套餐信息接口
		getFindList() {
			this.$http.get('/memberPackage/findList', {}).then(res => {
				this.goodsList = res.data
				console.log(this.goodsList)
			})
			// uni.request({
			// 		url: 'https://www.tailongshoudian.com/WNC/memberPackage/findList', //仅为示例，并非真实接口地址。
			// 		data: {},
			// 		header: {
			// 				'Content-Type': "application/x-www-form-urlencoded; charset=utf-8"
			// 		},
			// 		success: (res) => {
			// 				console.log(res.data);
			// 		}
			// });
		},
		// 获取首页轮播图数据接口
		getHeadBanner() {
			this.$http.get('/banner/getHeadBanner', {}).then(res => {
				console.log(res)
				this.indexSwiperList = res.data
			})
		},
		// 获取首页其他banner数据接口
		getCenterBanner() {
			this.$http.get('/banner/getCenterBanner', {}).then(res => {
				console.log(res)
				this.banner_id = res.data.id
				this.bannerImg = res.data.url
			})
			// uni.request({
			// 	url: "http://192.168.1.143:8086/WNC/banner/getCenterBanner",
			// 	data: {},
			// 	header: {
			// 		"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
			// 	},
			// 	succeee(res) {
			// 		console.log(res);
			// 	}
			// });
		},
		getAuthorizeInfo(a = "scope.userLocation") {
			//1. uniapp弹窗弹出获取授权（地理，个人微信信息等授权信息）弹窗
			uni.authorize({
				scope: a,
				success: () => {
					//1.1 允许授权
					this.getLocationInfo();
				},
				fail: () => {
					//1.2 拒绝授权
					// console.log("你拒绝了授权，无法获得周边信息");
					uni.showToast({
						title: "你拒绝了授权，无法获得周边信息",
						duration: 3000,
						icon: "none"
					});
					uni.getSetting({
						success: res => {
							console.log(res)
						}
					})
				}
			});
		},

		getLocationInfo() {
			//2. 获取地理位置
			uni.getLocation({
				type: "wgs84",
				success: res => {
					console.log("当前位置的经度：" + res.longitude);
					console.log("当前位置的纬度：" + res.latitude);
					this.latitude = res.latitude;
					this.longitude = res.longitude;
					console.log(this.latitude)
					console.log(this.longitude)
					// let latitude, longitude;
					// latitude = res.latitude.toString();
					// longitude = res.longitude.toString();
					// uni.request({
					//   header: {
					//     "Content-Type": "application/text"
					//   },
					//   url:
					//     "http://apis.map.qq.com/ws/geocoder/v1/?location=" +
					//     latitude +
					//     "," +
					//     longitude +
					//     "&key=MVGBZ-R2U3U-W5CVY-2PQID-AT4VZ-PDF35",
					//   success(re) {
					//     console.log("中文位置");
					//     console.log(re);
					//     if (re.statusCode === 200) {
					//       console.log("获取中文街道地理位置成功");
					//     } else {
					//       console.log("获取信息失败，请重试！");
					//     }
					//   }
					// });
				},
				fail: res => {
					console.log(res);
					if (res.errCode == 2) {
						uni.showToast({
							title: "请您打开GPS",
							duration: 3000,
							icon: "none"
						});
						// this.getLocationInfo()
					}
				}
			});
		},

		isGetLocation(a = "scope.userLocation") {
			// 3. 检查当前是否已经授权访问scope属性，参考下截图
			uni.getSetting({
				success: res => {
					if (!res.authSetting[a]) {
						//3.1 每次进入程序判断当前是否获得授权，如果没有就去获得授权，如果获得授权，就直接获取当前地理位置
						this.getAuthorizeInfo();
					} else {
						this.getLocationInfo();
					}
				}
			});
		},
		// 去绿色健康页面
		goGreenHealth() {
			uni.navigateTo({
				url: '/pages/greenHealth/greenHealth'
			})
		},
		// 去定位页面
		goIndexLocation() {
			uni.navigateTo({
				url: '/pages/indexLocation/indexLocation'
			})
		},
		// 去消息页面
		goIndexNews() {
			uni.navigateTo({
				url: '/pages/indexNews/indexNews'
			})
		},
		// 去正在开发页面
		goExpect() {
			uni.navigateTo({
				url: '/pages/expect/expect'
			})
		},
		// 去会员套餐页面详情
		goVipGoodsDetailsInfo(id) {
			uni.navigateTo({
				url: '/pages/vipGoodsDetailsInfo/vipGoodsDetailsInfo?id=' + id
			})
		},
		// 去会员赠送页面
		goVipGive() {
			uni.navigateTo({
				url: '/pages/vipGive/vipGive'
			})
		}
	}
};
